<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>
      
      <ui-panel title="Network Register" icon="users">

        <!-- :value="parseInt(memberPackage.price).formatMoney()"  -->
        <ui-box icon="cube" 
        :title="(form.payment_method == 'ecash')?'Pay With Ecash':'Pay Transfer'"
        :value="memberPackage.name" />
        <hr>

        <ui-row form-group>
          <ui-col size="md-1">Username</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.username" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Password</ui-col>
          <ui-col size="md-11">
            <ui-textbox type="password" v-model="form.member.password" ></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Konfirmasi Password</ui-col>
          <ui-col size="md-11">
            <ui-textbox type="password" v-model="form.member.confirmPassword" ></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">NIK</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.nik" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">First Name</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.first_name"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Last Name</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.last_name"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Gender</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rsGender"
            data-field="name"
            v-model="form.member.gender" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Tanggal Lahir</ui-col>
          <ui-col size="md-11">
            <ui-datepicker v-model="form.member.birth_day" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Email</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.email" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">No. Telepon</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.phone" />
          </ui-col>
        </ui-row>
        
        <ui-row form-group>
          <ui-col size="md-1">Alamat</ui-col>
          <ui-col size="md-11">
            <ui-textarea v-model="form.member.address"></ui-textarea>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Provinsi</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rsProvince_"
            @input="form.member.city_id = rsCity_[0].id; moveProvince(form.member.province_id)"
            data-field="name"
            v-model="form.member.province_id" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Kota</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rsCity_"
            data-field="name"
            v-model="form.member.city_id" 
            @input="moveCity(form.member.city_id)" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Kode Pos</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.post_code" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Ahli Waris</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.benificiery" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Hubungan Ahli Waris</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.benificiery_relation" />
          </ui-col>
        </ui-row>

        <hr>

        <ui-row form-group>
          <ui-col size="md-1">Nama Bank</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.bank_name" />
          </ui-col>
        </ui-row>
        <ui-row form-group>
          <ui-col size="md-1">Nama Pemilik Rekening</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.bank_acc_name" />
          </ui-col>
        </ui-row>
        <ui-row form-group>
          <ui-col size="md-1">Nomor Rekening</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.bank_acc_number" />
          </ui-col>
        </ui-row>
        <ui-row form-group>
          <ui-col size="md-1">Cabang Bank</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.bank_branch" />
          </ui-col>
        </ui-row>

        <hr>

        <ui-row form-group>
          <ui-col size="md-1">NPWP</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.npwp" />
          </ui-col>
        </ui-row>
        <ui-row form-group>
          <ui-col size="md-1">NPWP Registration Date</ui-col>
          <ui-col size="md-11">
            <ui-datepicker v-model="form.member.npwp_date" />
          </ui-col>
        </ui-row>
        <ui-row form-group>
          <ui-col size="md-1">NPWP Address</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.member.npwp_address" />
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="danger"
      icon="arrow-left" />

      <ui-button @click="showModal" 
      icon="floppy-o" />
    </ui-buttonbar>

    <ui-modal @close="modal = false" :display="modal" title="Cek Pendaftaran" icon="question">
      <div slot="message">

        <div style="height: 240px; over-flow-x: hidden; overflow-y: scroll; overflow-x: hidden">
          
          <ui-row form-group>
            <ui-col size="md-1">Username</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.username" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">NIK</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.nik" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">First Name</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.first_name" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Last Name</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.last_name" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Gender</ui-col>
            <ui-col size="md-11">
              <ui-label :value="getGenderById(form.member.gender)" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Tanggal Lahir</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.birth_day" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Email</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.email" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">No. Telepon</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.phone" />
            </ui-col>
          </ui-row>
          
          <ui-row form-group>
            <ui-col size="md-1">Alamat</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.address" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Provinsi</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="province.name" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Kota</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="city.name" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Kode Pos</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.post_code" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Ahli Waris</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.benificiery" />
            </ui-col>
          </ui-row>

          <ui-row form-group>
            <ui-col size="md-1">Hubungan Ahli Waris</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.benificiery_relation" />
            </ui-col>
          </ui-row>

          <hr>

          <ui-row form-group>
            <ui-col size="md-1">Nama Bank</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.bank_name" />
            </ui-col>
          </ui-row>
          <ui-row form-group>
            <ui-col size="md-1">Nama Pemilik Rekening</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.bank_acc_name" />
            </ui-col>
          </ui-row>
          <ui-row form-group>
            <ui-col size="md-1">Nomor Rekening</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.bank_acc_number" />
            </ui-col>
          </ui-row>
          <ui-row form-group>
            <ui-col size="md-1">Cabang Bank</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.bank_branch" />
            </ui-col>
          </ui-row>

          <hr>

          <ui-row form-group>
            <ui-col size="md-1">NPWP</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.npwp" />
            </ui-col>
          </ui-row>
          <ui-row form-group>
            <ui-col size="md-1">NPWP Registration Date</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.npwp_date" />
            </ui-col>
          </ui-row>
          <ui-row form-group>
            <ui-col size="md-1">NPWP Address</ui-col>
            <ui-col size="md-11">
              <ui-label v-model="form.member.npwp_address" />
            </ui-col>
          </ui-row>
          
        </div>

      </div>
      <div slot="buttonbar">
        <ui-button @click="saveData"
        :loading="loading">Konfirmasi</ui-button>

        <ui-button @click="modal = false" 
        bs-type="danger">Cancel</ui-button>
      </div>
    </ui-modal>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        modal: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          upline: '',
          position: '',
          payment_method: '',
          package: '',
          order: {
            quantity: [],
            product_id: [],
            stock_product_id: [],
          },
          member: {},
        },
      };
    },

    computed: {
      ...mapGetters('NetworkRegisterStore', {
        rs: 'rs',
        rowActive: 'rowActive',
        data: 'data',
      }),

      ...mapGetters('MemberPackageStore', {
        memberPackage: 'rowActive',
      }),

      ...mapGetters('GenderStore', {
        rsGender: 'rs',
        getGenderById: 'getNameById',
      }),
      
      ...mapGetters('ProvinceStore', {
        rsProvince: 'rs',
        province: 'rowActive',
      }),

      ...mapGetters('CityStore', {
        rsCity: 'rs',
        city: 'rowActive',
      }),

      rsProvince_() {
        let rsProvince = [];
        
        rsProvince.push({
          id: '',
          name: '--- Pilih Provinsi ---',
        });

        this.rsProvince.forEach((province) => {
          rsProvince.push(province);
        });

        return rsProvince;
      },

      rsCity_() {
        let rsCity = [];
        
        rsCity.push({
          id: '',
          name: '--- Pilih Kota ---',
          province_id: '',
          type: '',
        });
        
        this.rsCity.forEach((city) => {
          if (city.province_id == this.form.member.province_id) {
            rsCity.push(city);
          }
        });

        return rsCity;
      },

    },

    mounted() {
      this.reloadParams();
      this.refreshData();
    },
  
    methods: {
     
      ...mapActions('NetworkRegisterStore', {
        fetchData: 'fetchData',
        createData: 'createData',
        moveLastData: 'moveLastData',
      }),

      ...mapActions('ProvinceStore', {
        fetchProvince: 'fetchData',
        moveProvince: 'moveData',
      }),

      ...mapActions('CityStore', {
        fetchCity: 'fetchData',
        moveCity: 'moveData',
      }),

      reloadParams() {
        // this.form.payment_method = this.$route.params.paymentMethod;
        // this.form.package = this.$route.params.package;

        this.form.upline = this.data.upline;
        this.form.position = this.data.position;
        this.form.package = this.data.package;
        this.form.payment_method = this.data.payment_method;
        this.form.order = this.data.order;
      },

      async refreshData() {
        let data = {};
        this.loading = true;

        await this.fetchProvince();
        await this.fetchCity();
        await this.fetchData(data);

        this.loading = false;
      },

      showModal() {
        // if (this.total > 0) this.modal = true;
        this.modal = true;
      },

      saveData() {

        this.loading = true;

        this.createData(this.form).then(result => {
          this.moveLastData();

          this.loading = false;
          this.modal = false;
          this.result = result;

          this.splash = true;

          setTimeout(() => {
            this.splash = false;

            setTimeout(() => {
              this.$router.push('/network-register');
            }, 500);
          }, 1500);

        }).catch(result => {

          this.loading = false;
          this.modal = false;
          this.result = result;

        });
      },

    },

  }
</script>



