export default new class {

  init() {
    return new Promise((resolve, reject) => {

      // if (!localStorage.apiUrl)
      // localStorage.apiUrl = 'http://localhost/samtari/web/index.php/api/';
      // localStorage.apiUrl = 'http://139.59.244.196/samtari/web/index.php/api/';
      // localStorage.apiUrl = 'http://cl.smoorp.net/samtari/index.php/api/';
      localStorage.apiUrl = 'https://samtari-int.com/index.php/api/';

      $.ajax({
        method: 'GET',
        cache: false,
        url: localStorage.apiUrl + 'system/readConfig',

        success: (result) => {
          localStorage.apiKey = result.data.apiKey;
          localStorage.baseUrl = result.data.baseUrl;
          localStorage.archivesUrl = localStorage.baseUrl + '/archives';

          resolve(result);
        },
      }).fail((result) => {
        reject(result);
      });

    });
  }

  post(url, data = {}) {
    return new Promise((resolve, reject) => {
      data.lang = 'id';
      data.apiKey = localStorage.apiKey;
      if (localStorage.authKey)
        data.authKey = localStorage.authKey;

      url = localStorage.apiUrl + url;

      $.ajax({
        method: 'POST',
        cache: false,
        url,
        data,

        success: (result) => {

          // if denied re-init the apiKey
          if (result.state == 'denied') {
            this.init().then(_result => {

              // re-call the api -------------------------------------------- //

              data.apiKey = localStorage.apiKey;
              if (localStorage.authKey)
                data.authKey = localStorage.authKey;

              $.ajax({
                method: 'POST',
                cache: false,
                url,
                data,

                success: (result) => {
                  resolve(result);
                }
              }).fail((result) => {
                reject(result);
              });

              // ------------------------------------------------------------ //

            }).catch(_result => {
              reject(result);
            });
          }
          else
            resolve(result);
        }
      }).fail((result) => {
        reject(result);
      });

    });
  }

}



