// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import 'typeface-ubuntu';
import 'font-awesome/css/font-awesome.min.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import 'animate.css/animate.min.css';

import './assets/styles';

import $ from 'jquery'; window.$ = window.jQuery = $;
// import bootstrap from 'bootstrap/dist/js/bootstrap.min.js'; bootstrap.$ = $;

import Vue from 'vue'; Vue.config.productionTip = false;

import App from './App';
import api from './api'; window.api = api;
import store from './store';
import router from './router';

import './components';
import './layouts';

Number.prototype.formatMoney = function(c, d, t){
  var n = this, 
  c = isNaN(c = Math.abs(c)) ? 2 : c, 
  d = d == undefined ? "," : d, 
  t = t == undefined ? "." : t, 
  s = n < 0 ? "-" : "", 
  i = String(parseInt(n = Math.abs(Number(n) || 0).toFixed(c))), 
  j = (j = i.length) > 3 ? j % 3 : 0;
  return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
};

/* eslint-disable no-new */

api.init().then(() => {
  new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App },
  });
}).catch((result) => {

  console.log('Api initialize failed');

  new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App },
  });
});



