export default {
  namespaced: true,

  /************************
  function _state()
  ************************/
  state: {
    rs: [
      { id: 0, status: 'Arsip' },
      { id: 1, status: 'Dipublikasikan' },
    ],
  },

  /************************
  function _getters()
  ************************/
  getters: {

    rs(state) {
      return state.rs;
    },

  },

}



