<template>
  <main-layout id="UpdateProfileView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Update Profile" icon="edit" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-1">Avatar</ui-col>
          <ui-col size="md-11">
            <ui-image v-model="form.avatar" upload="true" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">NIK</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.nik"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">First Name</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.first_name"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Last Name</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.last_name"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Gender</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rsGender"
            data-field="name"
            v-model="form.gender" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Tanggal Lahir</ui-col>
          <ui-col size="md-11">
            <ui-datepicker v-model="form.birth_day" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Email</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.email" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">No. Telepon</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.phone" />
          </ui-col>
        </ui-row>
        
        <ui-row form-group>
          <ui-col size="md-1">Alamat</ui-col>
          <ui-col size="md-11">
            <ui-textarea v-model="form.address"></ui-textarea>
          </ui-col>
        </ui-row>
        
        <ui-row form-group>
          <ui-col size="md-1">Provinsi</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rsProvince"
            @input="form.city_id = rsCity_[0].id"
            data-field="name"
            v-model="form.province_id" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Kota</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rsCity_"
            data-field="name"
            v-model="form.city_id" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Kode Pos</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.post_code" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Ahli Waris</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.benificiery" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Hubungan Ahli Waris</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.benificiery_relation" />
          </ui-col>
        </ui-row>

        <!-- 

        <ui-row form-group>
          <ui-col size="md-1">NPWP</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.npwp"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">NPWP Registration Date</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.npwpRegistrationDate"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">NPWP Address</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.npwpAddress"></ui-textbox>
          </ui-col>
        </ui-row> -->

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="success"
      icon="arrow-left" 
      :disabled="loading" />

      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" 
      :disabled="loading" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {},
      };
    },

    computed: {
      ...mapGetters('AccountStore', {
        rowActive: 'myAccount',
      }),
      ...mapGetters('GenderStore', {
        rsGender: 'rs',
      }),
      ...mapGetters('ProvinceStore', {
        rsProvince: 'rs',
      }),
      ...mapGetters('CityStore', {
        rsCity: 'rs',
      }),

      rsCity_() {
        return this.rsCity.filter(city => city.province_id == this.form.province_id);
      },
    },

    async mounted() {
      this.loading = true;

      await this.fetchProvince();
      await this.fetchCity();
      await this.reload();
      
      this.form = this.rowActive.member;

      this.loading = false;
    },

    methods: {

      ...mapActions('AccountStore', {
        reload: 'reload',
        updateData: 'updateProfile',
      }),
      ...mapActions('ProvinceStore', {
        fetchProvince: 'fetchData',
      }),
      ...mapActions('CityStore', {
        fetchCity: 'fetchData',
      }),

      saveData() {
        this.loading = true;

        this.updateData(this.form).then(result => {

          this.loading = false;
          this.result = result;

          this.splash = true;

          setTimeout(() => {
            this.splash = false;
            this.$router.go(-1);
          }, 2000);

        }).catch(result => {

          this.loading = false;
          this.result = result;

        });
      },

    },

  }
</script>



