<template>
  <main-layout id="BonusLeadershipEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Bonus Leadership" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Level Id</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.level_id"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Name</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.name"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">PV</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.pv"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Bonus</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.bonus"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Date</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.date"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Status</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.status"></ui-label>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />

    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          level_id: '',
          name: '',
          pv: '',
          bonus: '',
          date: '',
          status: '',
        },
      };
    },

    /************************
    <id="_computed">
    ************************/
    computed: {
      /************************
      <id="_mapGetters">
      ************************/
      ...mapGetters('BonusLeadershipStore', {
        rowActive: 'rowActive',
      }),
    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.level_id = this.rowActive.level_id;
      this.form.name = this.rowActive.name;
      this.form.pv = this.rowActive.pv;
      this.form.bonus = this.rowActive.bonus;
      this.form.date = this.rowActive.date;
      this.form.status = this.rowActive.status;

    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_mapActions">
      ************************/
      ...mapActions('BonusLeadershipStore', {
        moveData: 'moveData',
        updateData: 'updateData',
      }),


    },

  }
</script>



