<template>
  <main-layout id="SalesOrderByRegisterAllDataView">

    <ui-container fluid>
      <ui-panel title="Sales Order By Register" :loading="loading" icon="list">

        <ui-row form-group>
          <ui-col size="md-12">

            <ui-list @tap="openData"
            :data-source="rs"
            data-field="bv" />

          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        loading: false,
        deleteDialog: false,
      };
    },

    /************************
    <id="_computed">
    ************************/
    computed: {

      /************************
      <id="_mapGetters">
      ************************/
      ...mapGetters('SalesOrderByRegisterStore', {
        rs: 'rs',
        rowActive: 'rowActive',
      }),

    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
      if (this.rs.length <= 0)
        this.refreshData();
    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_mapActions">
      ************************/
      ...mapActions('SalesOrderByRegisterStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
      }),

      /************************
      <id="_refreshData">
      ************************/
      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      /************************
      <id="_openData">
      ************************/
      openData(value) {
        this.$router.push('/sales-order-by-register/edit-data/' + value);
      },

    },

  }
</script>



