export default {
  namespaced: true,

  /************************
  function _state()
  ************************/
  state: {
    rs: [
      { id: 1, month: 'Januari' },
      { id: 2, month: 'Februari' },
      { id: 3, month: 'Maret' },
      { id: 4, month: 'April' },
      { id: 5, month: 'Mei' },
      { id: 6, month: 'Juni' },
      { id: 7, month: 'Juli' },
      { id: 8, month: 'Agustus' },
      { id: 9, month: 'September' },
      { id: 10, month: 'Oktober' },
      { id: 11, month: 'Nopember' },
      { id: 12, month: 'Desember' },
    ],
  },

  /************************
  function _getters()
  ************************/
  getters: {

    rs(state) {
      return state.rs;
    },

  },

}



