<style scoped>
  .box {
    width: 100%;
    height: 100px;
    border: solid black 1px;
    background: #449d44;
    box-shadow: silver 2px 2px 2px;
    text-align: center;
    display: block;
  }

  .box:hover {
    text-decoration: none;
    opacity: 0.9;
  }

  .box label {
    color: rgba(0, 0, 0, 0.7);
    display: block;
    font-size: 14px;
    color: #F0F0F0;
    margin-top: 8px;
  }

  .box b {
    color: white;
    font-size: 18px;
    text-shadow: black 2px 2px 2px;
  }
</style>

<template>
  <a @click="handleClick" :href="href" class="box">
    <label>{{ title }}</label>
    <b>
      <div v-if="icon"><i class="fa" :class="'fa-' + icon"></i></div>
      <div>{{ value }}</div>
    </b>
  </a>
</template>

<script>
  export default {

    props: {

      title: {
        type: String,
        default: 'Title',
      },

      icon: {
        type: String,
        default: '',
      },

      value: {
        type: String,
        default: 'Value',
      },
      
      href: {
        type: String,
        default: 'javascript:',
      },

    },

    methods: {
      handleClick() {
        this.$emit('click');
      },
    },

  }
</script>



