<template>
  <main-layout id="BonusRewardAllDataView">

    <ui-container fluid>
      <ui-panel title="Bonus Reward" :loading="loading" icon="list">

        <ui-row form-group>
          <ui-col size="md-12">
            <ui-col v-if="rs.length > 0">Reward Jalan jalan</ui-col>
            <ui-list @tap="openData"
            :data-source="rs"
            data-field="bv" />
            
            <br>
            <ui-col v-if="rsKlaim.length > 0">Riwayat Klaim Bonus Reward/Bonanza</ui-col>
            <ui-list @tap="openDataList"
            :data-source="rsKlaim"
            data-field="bv"/>


          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        deleteDialog: false,
      };
    },

    computed: {

      ...mapGetters('BonusRewardStore', {
        rs: 'rs',
        rsKlaim: 'rsKlaim',
        rowActive: 'rowActive',
        rowActiveKlaim: 'rowActiveKlaim',
      }),

    },

    mounted() {
      if (this.rs.length <= 0)
        this.refreshData();
    },

    methods: {

      ...mapActions('BonusRewardStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
        moveDataKlaim: 'moveDataKlaim',
      }),

      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      updateData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      openData(value) {
        this.$router.push('/bonus-reward/detail-data/' + value);
      },

      openDataList(value) {
        this.$router.push('/bonus-reward/detail-data-klaim/' + value);
      },

    },

  }
</script>



