<template>
  <main-layout id="SalesOrderTransactionEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Sales Order Transaction" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Code</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.code"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Total Item</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.total_item"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Total Price</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.total_price"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Total Kiri</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.total_left"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Tipe</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.type"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Cashback</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.is_cashback"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Status</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.status"></ui-label>
          </ui-col>
        </ui-row>


      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />


    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          code: '',
          total_item: '',
          total_price: '',
          total_left: '',
          type: '',
          is_cashback: '',
          status: '',
        },
      };
    },

    computed: {
      ...mapGetters('SalesOrderTransactionStore', {
        rowActive: 'rowActive',
      }),
    },

    mounted() {
      this.moveData(this.$route.params.id);
  
      this.form.id = this.rowActive.id;
      this.form.code = this.rowActive.code;
      this.form.total_item = this.rowActive.total_item;
      this.form.total_price = this.rowActive.total_price;
      this.form.total_left = this.rowActive.total_left;
      this.form.type = this.rowActive.type;
      this.form.is_cashback = this.rowActive.is_cashback;
      this.form.status = this.rowActive.status;

    },

    methods: {

      ...mapActions('SalesOrderTransactionStore', {
        moveData: 'moveData',
        updateData: 'updateData',
      }),


    },

  }
</script>



