<template>
  <main-layout id="BonusRewardDetailKlaimDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Bonus Reward Klaim" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Name</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.name"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Syarat PR Kanan dan Kiri</ui-col>
          <ui-col size="md-5">
            <ui-label v-model="form.pv"></ui-label>
          </ui-col>
          <ui-col size="md-5">
            <ui-label v-model="form.pv"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Bonus</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.bonus"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Date</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.date"></ui-label>
          </ui-col>
        </ui-row>
        

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="success"
      icon="arrow-left" />

    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          name: '',
          bonus: '',
          pv: '',
          date: '',
          status: '',
        },
      };
    },

    computed: {
      ...mapGetters('BonusRewardStore', {
        rowActiveKlaim: 'rowActiveKlaim',
      }),
    },

    mounted() {
      this.moveDataKlaim(this.$route.params.id);

      this.form.id = this.rowActiveKlaim.id;
      this.form.name = this.rowActiveKlaim.name;
      this.form.bonus = this.rowActiveKlaim.bonus;
      this.form.pv = this.rowActiveKlaim.pv;
      this.form.date = this.rowActiveKlaim.date;
      this.form.status = this.rowActiveKlaim.status;

    },

    methods: {

      ...mapActions('BonusRewardStore', {
        moveDataKlaim: 'moveDataKlaim',
      }),


    },

  }
</script>



