export default {
  namespaced: true,

  state: {
    rs: [],
    rowActive: {},
    rsYear: [],
  },

  mutations: {

    setDataSource(state, dataSource) {
      state.rs = dataSource;
    },

    setRsYear(state, data) {
      state.rsYear = data;
    },

    addData(state, data) {
      state.rs.push(data);
    },

    setData(state, data) {
      state.rs.splice(state.rowActive.index, 1, {
        ...state.rs[state.rowActive.index],
        ...data,
      });
    },

    removeData(state) {
      state.rs.splice(state.rowActive.index, 1);
      state.rowActive = {};
    },

    setRowActiveByIndex(state, index) {
      let rowActive = state.rs[index];
      rowActive.index = index;

      state.rowActive = rowActive;
    },

    setRowActiveById(state, id) {

      let rowActive = {};
      let i = -1;

      state.rs.forEach(row => {
        i++;

        if (row.id == id) {
          rowActive = row;
          rowActive.index = i;
        }

      });

      state.rowActive = rowActive;
    },

  },

  actions: {

    fetchData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('bonusCashback/readAllData', data).then(result => {

          if (result.state == 'success') {
            commit('setDataSource', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    fetchYear({ commit }) {
      return new Promise((resolve, reject) => {
        api.post('bonusCashback/readAllYear').then(result => {

          if (result.state == 'success') {
            commit('setRsYear', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },


    moveData({ commit }, id) {
      commit('setRowActiveById', id);
    },

  },

  getters: {

    rs(state) {
      return state.rs;
    },

    rsYear(state) {
      let xYear;

      return state.rsYear.map(row => {
        xYear = row.date.split('-');
        row.year = xYear[0];

        return row;
      });
    },

    rowActive(state) {
      return state.rowActive;
    },

  },

}



