<style scoped>
  .ui-navbar {
    background: transparent;
  }

  .ui-navbar li > ul li {
    border-left: none;
  }

  .ui-navbar li a {
    color: rgba(255, 255, 255, 0.7);
  }

  .ui-navbar li a i:first-child {
    width: 28px;
  }

  .ui-navbar li a:hover {
    background: transparent;
    color: white;
  }

  .nav > li > a:focus {
    background: transparent;
  }

  .ui-navbar li.active a {
    background: rgba(255, 255, 255, 0.1);
  }
</style>

<template>
  <nav id="NavbarComponent" class="ui-navbar navbar" :class="'navbar-' + type">
    <div class="container-fluid">
      <ul class="nav">

        <li v-for="(menu, key) in dataSource"
        :key="key"
        :class="(menu.collapse?'collapse':'') + ' ' + (menu.permalink == hash?'active':'')">
          <a v-if="!menu.child"
          :href="menu.permalink">
            <i v-if="menu.icon" class="fa" :class="'fa-' + menu.icon"></i>  
            <span v-html="menu.title"></span>
          </a>

          <a v-if="menu.child" @click="toggleCollapse(menu)" href="javascript:">
            <i v-if="menu.icon" class="fa" :class="'fa-' + menu.icon"></i>
            {{ menu.title }}
            <i v-if="menu.child" class="chevron fa fa-chevron-right"></i>
          </a>

          <ul v-if="menu.child" class="nav">
            <li v-for="(subMenu, k) in menu.child" 
            :key="k"
            :class="subMenu.permalink == hash?'active':''">
              <a :href="subMenu.permalink">
                <i v-if="subMenu.icon" class="fa" :class="'fa-' + subMenu.icon"></i>
                {{ subMenu.title }}
              </a>
            </li>
          </ul>
        </li>

      </ul>
    </div>
  </nav>
</template>

<script>
  export default {

    /************************
    <id="_props">
    ************************/
    props: {

      type: {
        type: String,
        default: 'default',
      },

      dataSource: {
        type: Object,
        default: () => {},
      },

    },

    /************************
    <id="_computed">
    ************************/
    computed: {

      /************************
      <id="_hash">
      ************************/
      hash() {
        return location.hash;
      },

    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_toggleCollapse">
      ************************/
      toggleCollapse(menu) {
        menu.collapse = !menu.collapse;
      },

    },

  }
</script>



