export default {
  namespaced: true,

  /************************
  function _state()
  ************************/
  state: {
    rs: [],
    rowActive: {},
  },

  /************************
  function _mutations()
  ************************/
  mutations: {

    setDataSource(state, dataSource) {
      state.rs = dataSource;
    },

    addData(state, data) {
      state.rs.push(data);
    },

    setData(state, data) {
      state.rs.splice(state.rowActive.index, 1, {
        ...state.rs[state.rowActive.index],
        ...data,
      });
    },

    removeData(state) {
      state.rs.splice(state.rowActive.index, 1);
      state.rowActive = {};
    },

    setRowActiveByIndex(state, index) {
      let rowActive = state.rs[index];
      rowActive.index = index;

      state.rowActive = rowActive;
    },

    setRowActiveById(state, id) {

      let rowActive = {};
      let i = -1;

      state.rs.forEach(row => {
        i++;

        if (row.id == id) {
          rowActive = row;
          rowActive.index = i;
        }

      });

      state.rowActive = rowActive;
    },

  },

  actions: {

    fetchData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('registerMember/readAllData', data).then(result => {

          if (result.state == 'success') {
            commit('setDataSource', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    moveFirstData({ commit }) {
      commit('setRowActiveByIndex', 0);
    },

    moveLastData({ state, commit }) {
      commit('setRowActiveByIndex', state.rs.length - 1);
    },

    moveData({ commit }, id) {
      commit('setRowActiveById', id);
    },

    createData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('registerMember/createData', data).then(result => {

          if (result.state == 'success') {
            commit('addData', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    updateData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('registerMember/updateData', data).then(result => {
          if (result.state == 'success')
            resolve(result);
          else
            reject(result);
        });

        commit('setData', data);
      });
    },

    deleteData({ state, commit }, id) {
      return new Promise((resolve, reject) => {
        api.post('registerMember/deleteData', { id: state.rowActive.id }).then(result => {
          if (result.state == 'success')
            resolve(result);
          else
            reject(result);
        });

        commit('removeData');
      });
    },

  },

  /************************
  function _getters()
  ************************/
  getters: {

    rs(state) {
      return state.rs;
    },

    rowActive(state) {
      return state.rowActive;
    },

  },

}



