<template>
  <main-layout>
    
    <ui-container fluid>
      <ui-panel title="Upgrade Membership" icon="cube">

        <div v-if="packages.length <= 0">
          <ui-alert :display="true" :close-button="false">
            Anda telah memiliki paket tertinggi
          </ui-alert>
        </div>
        <div v-else>
          <h5>Please select the package member;</h5>
          <hr>
          <div v-for="(memberPackage, i) in packages" 
          :key="i"
          style="padding-bottom: 16px">
            <ui-row>
              <ui-col size="xs-6">
                <!-- :value="parseInt(memberPackage.price).formatMoney()"  -->
                <ui-box icon="cube" 
                title="Pay With Ecash" 
                @click="selectPackage('ecash', memberPackage.id)"
                :value="memberPackage.name" />
              </ui-col>

              <ui-col size="xs-6">
                <!-- :value="parseInt(memberPackage.price).formatMoney()"  -->
                <ui-box icon="cube" 
                title="Pay Transfer" 
                @click="selectPackage('transfer', memberPackage.id)"
                :value="memberPackage.name" />
              </ui-col>
            </ui-row>        
          </div>
        </div>

        <hr>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
      };
    },

    mounted() {
      this.refreshData();
    },

    computed: {

      ...mapGetters('MemberPackageStore', {
        packages: 'rs',
      }),

    },

    methods: {

      ...mapActions('UpgradeMembershipStore', {
        updateData: 'updateData',
      }),

      ...mapActions('MemberPackageStore', {
        fetchPackages: 'fetchTopPackages',
        movePackage: 'moveData',
      }),

      selectPackage(paymentMethod, packageId) {
        this.movePackage(packageId);
        this.updateData({
          payment_method: paymentMethod,
          package: packageId,
        });
        this.$router.push('/upgrade-membership/' + paymentMethod + '/' + packageId);
      },

      refreshData() {
        this.loading = true;
        this.fetchPackages().then(result => {
          this.loading = false;
        });
      },

    },

  }
</script>



