<template>
  <main-layout id="BonusCashbackEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Bonus Cashback" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">From</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.from_id"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Level</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.level"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Bonus</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.bonus"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Idx</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.idx"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Date</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.date"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Status</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.status"></ui-label>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />

    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          from_id: '',
          level: '',
          bonus: '',
          idx: '',
          date: '',
          status: '',
        },
      };
    },

    computed: {
      ...mapGetters('BonusCashbackStore', {
        rowActive: 'rowActive',
      }),
    },

    mounted() {
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.from_id = this.rowActive.from_id;
      this.form.level = this.rowActive.level;
      this.form.bonus = this.rowActive.bonus;
      this.form.idx = this.rowActive.idx;
      this.form.date = this.rowActive.date;
      this.form.status = this.rowActive.status;

    },

    methods: {

      ...mapActions('BonusCashbackStore', {
        moveData: 'moveData',
      }),


    },

  }
</script>



