<template>
  <div id="LabelComponent" class="ui-label">

    <label v-if="this.$slots.default">
      <slot></slot>
    </label>

    <label style="border-bottom: silver solid 1px; display: block">
      {{ (value)?value:'[ Empty ]' }}
    </label>

    <!-- <span class="icon">
      <i v-if="!loading" class="fa" :class="icon_"></i>
      <i v-if="loading" class="fa fa-spin fa-refresh"></i>
    </span> -->
  </div>
</template>

<script>
  export default {

    /************************
    <id="_props">
    ************************/
    props: {
      type: {
        type: String,
        default: 'text',
      },

      value: {
        type: String,
        default: '',
      },

      icon: {
        type: String,
        default: '',
      },

      disabled: {
        type: Boolean,
        default: false,
      },

      loading: {
        type: Boolean,
        default: false,
      },

    },

    /************************
    <id="_computed">
    ************************/
    computed: {

      /************************
      <id="_icon_">
      ************************/
      icon_() {
        if (this.icon)
          return 'fa-' + this.icon;
        else
          return '';
      },
    },

  }
</script>



