<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>
      
      <ui-panel title="Registration Simulation" icon="users">

        <ui-box icon="cube" 
        :title="memberPackage.name" 
        :value="parseInt(memberPackage.price).formatMoney()" />
        <hr>
        
        <ui-row v-for="(row, i) in rs" :key="i" form-group>
          <ui-col size="md-12">
            <b>{{ row.name }}</b><br>
            <i>Price: {{ parseInt(row.price).formatMoney() }}</i>
          </ui-col>
          <ui-col size="md-12">
            <ui-textbox v-model="row.qty" placeholder="Qty" />
          </ui-col>
          <ui-col size="md-12">
            <i>Total: {{ parseInt(row.price * row.qty).formatMoney() }}</i>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="danger"
      icon="arrow-left" />

      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />
      
      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        loading: false,
        splash: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          title: '',
          content: '',
          publicationId: '',
          postCategoryId: '',
          featuredImage: '',
        },
      };
    },

    /************************
    <id="_computed">
    ************************/
    computed: {
      ...mapGetters('RegistrationSimulationStore', {
        rs: 'rs',
        rowActive: 'rowActive',
      }),

      ...mapGetters('MemberPackageStore', {
        memberPackage: 'rowActive',
      }),
    },

    /************************
    <id="_mounted">
    ************************/
   mounted() {
     this.refreshData();
    },

   methods: {
     
     ...mapActions('RegistrationSimulationStore', {
        fetchData: 'fetchData',
        createData: 'createData',
        moveLastData: 'moveLastData',
      }),

      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      saveData() {
        this.loading = true;

        this.createData(this.form).then(result => {
          this.moveLastData();

          this.loading = false;
          this.result = result;

          this.splash = true;

          setTimeout(() => {
            this.splash = false;

            setTimeout(() => {
              this.$router.push('/register-member');
            }, 500);
          }, 1500);

        }).catch(result => {

          this.loading = false;
          this.result = result;

        });
      },

    },

  }
</script>



