<template>
  <div id="ListComponent" class="ui-list">
    <!-- <i v-if="loading" class="fa fa-spin fa-refresh"></i> -->
      <slot></slot>
      <div v-if="dataSource.length > 0">
        <a href="javascript:"
        @click="handleClick(row[dataValue])"
        v-for="row in dataSource"
        :key="row[dataValue]"
        class="list-item"
        style="">
          <sup v-if="row[dataExtra]"
          style="color: gray">
            {{ row[dataExtra] }}
          </sup>
          <div>
            {{ row[dataField] }}
            <i class="fa fa-chevron-right"></i>
          </div>
        </a>
      </div>
  </div>
</template>

<style scoped>
  .list-item {
    border-bottom: solid #C0C0C0 1px;
    padding: 16px;
    padding-right: 24px;
    display: block;
    position: relative;
    color: black;
  }

  .list-item:hover {
    text-decoration: none;
    background: #F0F0F0;
  }

  .list-item i {
    position: absolute;
    top: 42%;
    right: 8px;
  }
</style>

<script>
  export default {

    /************************
    <id="_props">
    ************************/
    props: {

      dataSource: {
        type: Array,
        default: () => [],
      },

      dataValue: {
        type: String,
        default: 'id',
      },

      dataField: {
        type: String,
        default: '',
      },

      dataExtra: {
        type: String,
        default: '',
      },

      value: {
        type: String,
        default: '',
      },

      disabled: {
        type: Boolean,
        default: false,
      },

      loading: {
        type: Boolean,
        default: false,
      },

    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_handleClick">
      ************************/
      handleClick(value) {
        this.$emit('tap', value);
      },
    },

  }
</script>



