export default {
  namespaced: true,

  state: {
    rowActive: {},
  },

  mutations: {

    setDataSource(state, dataSource) {
      state.rowActive = dataSource;
    },


  },

  actions: {

    fetchData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('statementBonus/readAllData', data).then(result => {

          if (result.state == 'success') {
            commit('setDataSource', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

  },

  getters: {

    rowActive(state) {
      return state.rowActive;
    },

  },

}



