export default {
  namespaced: true,

  state: {
    row: {
      authKey: '',
      member: {},
    },
  },

  mutations: {

    setDataSource(state, dataSource) {
      state.row = {
        authKey: dataSource.authKey,
        member: dataSource.member,
      };
    },

  },

  actions: {

    reload({ commit }) {
      return new Promise((resolve, reject) => {
        api.post('account/profile').then(result => {
          if (result.state == 'denied') window.location = '#/login';
          commit('setDataSource', {
            authKey: localStorage.authKey,
            member: result.data,
          });

          resolve(result);
        });
      });
    },

    login({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('account/login', data).then(result => {

          if (result.state == 'success') {

            localStorage.authKey = result.data.authKey;

            commit('setDataSource', {
              authKey: localStorage.authKey,
              member: result.data.member,
            });
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    logout({ commit }) {
      return new Promise((resolve, reject) => {
        api.post('account/logout').then(result => {

          if (result.state == 'success') {

            localStorage.authKey = '';

            commit('setDataSource', {
              authKey: localStorage.authKey,
              member: {},
            });
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    reset({ commit }) {
      localStorage.authKey = '';

      commit('setDataSource', {
        authKey: localStorage.authKey,
        member: {},
      });
    },

    updateProfile({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('account/updateProfile', data).then(result => {

          if (result.state == 'success') {

            commit('setDataSource', {
              authKey: localStorage.authKey,
              member: result.data,
            });
            resolve(result);
          } else
            reject(result);
        });
      });
    },

  },

  getters: {

    isAuthen(state) {
      return (localStorage.authKey?true:false);
    },

    myAccount(state) {
      return state.row;
    },

  },

}



