export default {
  namespaced: true,

  /************************
  function _state()
  ************************/
  state: {
    rs: [
      { id: 1, name: 'Male' },
      { id: 0, name: 'Female' },
    ],
  },

  /************************
  function _getters()
  ************************/
  getters: {

    rs(state) {
      return state.rs;
    },

    getNameById(state) {
      let genderName = '';

      return (id) => {
        state.rs.forEach(row => {
          if (row.id == id) genderName = row.name;
        });
        
        return genderName;
      };
    },

  },

}



