<template>
  <main-layout id="GenealogySponsorAllDataView">

    <ui-container fluid>
      <ui-panel title="Genealogy Sponsor" :loading="loading" icon="list">
        
        <ui-row form-group>
          <ui-col size="md-2">Select Level</ui-col>
          <ui-col size="md-10">
            <ui-select :data-source="rsLevel"
            v-model="form.level"
            @input="refreshData"
            data-field="level"  />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-12">

            <ui-list @tap="openData"
            :data-source="rs"
            data-field="bv" />

          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        deleteDialog: false,

        form: {
          level:1,
        },

      };
    },

    computed: {

      ...mapGetters('GenealogySponsorStore', {
        rs: 'rs',
        rowActive: 'rowActive',
      }),
      ...mapGetters('LevelStore', {
        rsLevel: 'rs',
      }),

    },

    mounted() {
      if (this.rs.length <= 0)
        this.refreshData();
    },

    methods: {

      ...mapActions('GenealogySponsorStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
      }),

      refreshData() {
        this.loading = true;
        this.fetchData(this.form).then(result => {
          this.loading = false;
        });
      },

      openData(value) {
        this.$router.push('/genealogy-sponsor/edit-data/' + value);
      },

    },

  }
</script>



