<template>
  <main-layout id="StatementBonusAllDataView">

    <ui-container fluid>
      <ui-panel title="Statement Bonus" :loading="loading" icon="list">

        <ui-row form-group>
          <ui-col size="md-1">Period</ui-col>
          <ui-col size="md-5">
            <ui-datepicker v-model="form.periodSebelum" @input="refreshData()" />
          </ui-col>

          <ui-col size="md-6">
            <ui-datepicker v-model="form.periodSesudah" @input="refreshData()" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Sponsor Bonus (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.bsponsor" />
          </ui-col>
        </ui-row>
      
        <ui-row form-group>
          <ui-col size="md-3">Pairing Bonus (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.bpairing" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Qualified Bonus (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.bqualified" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Super Match Bonus (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.bmatching" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Cashback Bonus (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.bcashback" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Bonanza Bonus (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.bbonanza" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Total Bonus (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.sub" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">PPH Pasal 17 (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.tax" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Total Diterima (Rp.)</ui-col>
          <ui-col size="md-9">
            <ui-label v-model="rowActive.total" />
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      let d = new Date();
      let myDateSebelum, myDateSesudah;
      myDateSebelum = d.getFullYear() + '-' + (d.getMonth() +1) + '-1';
      d.setMonth(d.getMonth()+1);
      myDateSesudah = d.getFullYear() + '-' + (d.getMonth() +1) + '-1';

      return {
        loading: false,
        deleteDialog: false,
      
        form: {
          periodSebelum: myDateSebelum,
          periodSesudah: myDateSesudah,
        },

      };
    },
    computed: {

      ...mapGetters('StatementBonusStore', {
        rowActive: 'rowActive',
      }),

    },

    mounted() {
      this.refreshData();
    },

    methods: {

      ...mapActions('StatementBonusStore', {
        fetchData: 'fetchData',
      }),

      async refreshData() {
        this.loading = true;

        await this.fetchData(this.form);

        this.loading = false;
      },
      

    },

  }
</script>



