<style scoped>
  #btn-login {
    width: 100% !important;
    height: 48px !important;
  }
</style>

<template>

  <section>
    <ui-container fluid>
      <center>
        <img align="center" src="../../assets/images/logo.png" alt="">
        <br>&nbsp;
      </center>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Login" icon="lock" :loading="loading">


        <ui-row form-group>
          <ui-col size="xs-12">
            <ui-textbox placeholder="Type Username..." 
            v-model="form.username"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="xs-12">
            <ui-textbox placeholder="Type Password..." 
            v-model="form.password" type="password"></ui-textbox>
          </ui-col>
        </ui-row>

      </ui-panel>

      <ui-row form-group >
        <ui-col size="md-12">
          <button id="btn-login" @click="login" 
          class="btn btn-primary">
            <i v-if="loading" class="fa fa-spin fa-refresh"></i>
            <span v-else>Login</span>
          </button>
        </ui-col>
      </ui-row>

    </ui-container>
  </section>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          username: '',
          password: '',
        },
      };
    },

    methods: {

      ...mapActions('AccountStore', {
        'accountLogin': 'login',
      }),

      login() {
        this.loading = true;

        this.accountLogin(this.form).then(result => {

          this.loading = false;
          this.$router.push('/');

        }).catch(result => {
          this.loading = false;
          this.result = result;

        });
      },
    },

  }
</script>



