<style scoped>
  .logo {
    width: calc(100% - 128px);
    margin-left: 64px;
  }

  .logo img {
    width: 52px;
    position: absolute;
    left: 50%;
    margin-left: -26px;
    top: 0;
  }

  #SidebarComponent .info {
    color: white;
    border-bottom: double rgba(255, 255, 255, 0.1) 4px;
  }

  #SidebarComponent .info figure h4 {
    font-size: 13px;
  }

  #LinkComponent {
    color: #FFFFFF;
    padding: 1px;
    padding-left: 6px;
    padding-right: 6px;
    border-radius: 2px;
    /* font-weight: bold; */
    background: #286090;
  }
</style>

<template>
  <div id="MainLayout">

    <ui-sidebar>
      <div slot="menu">

        <div class="info" v-if="myAccount.authKey">
          <figure>
            <img v-if="myAccount.member.avatar" 
            :src="avatar">
            <small>{{ myAccount.member.nik }}</small>
            <h4>{{ myAccount.member.fullname }}</h4>
            <ui-link @click="logoutDialog = true" icon="sign-out">
              Logout
            </ui-link>
            <div class="clear"></div>
          </figure>
        </div>

        <ui-navbar :data-source="menu"></ui-navbar>
      </div>

      <div slot="header">
        <div class="logo">
          <img src="../assets/images/logo.png" 
          alt="">
        </div>
        <!-- <a href="#" style="float: right">
          <i class="fa fa-bars"></i>
        </a> -->
      </div>

    </ui-sidebar>

    <slot></slot>

    <ui-modal @close="logoutDialog = false" :display="logoutDialog" title="Confirm" icon="question">
      <div slot="message">Logout from Application?</div>
      <div slot="buttonbar">
        <ui-button @click="logout" :loading="logoutLoading">OK</ui-button>
        <ui-button @click="logoutDialog = false" bs-type="danger">Cancel</ui-button>
      </div>
    </ui-modal>

  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        logoutLoading: false,
        logoutDialog: false,
      }
    },

    /************************
    <id="_computed">
    ************************/
    computed: {
      /************************
      <id="_mapGetters">
      ************************/
      ...mapGetters('AccountStore', {
        accountIsAuthen: 'isAuthen',
        myAccount: 'myAccount',
      }),
      ...mapGetters('MenuStore', {
        menu: 'data',
      }),

      avatar() {
        return localStorage.archivesUrl + '/' + this.myAccount.member.avatar;
      },
    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
      this.accountReload();
    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_mapActions">
      ************************/
      ...mapActions('AccountStore', {
        accountReload: 'reload',
        accountLogout: 'logout',
      }),

      /************************
      <id="_logout">
      ************************/
      logout() {
        this.logoutLoading = true;

        this.accountLogout().then(result => {

          this.logoutLoading = false;
          this.logoutDialog = false;

          this.$router.push('/login' );

        }).catch(result => {

          this.logoutLoading = false;
          this.logoutDialog = false;

          console.log('Logout failed');
        });
      },
    },

  }
</script>



