<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-panel title="Update Profile" icon="eye" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-1">Avatar</ui-col>
          <ui-col size="md-11">
            <ui-image v-model="form.avatar" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">NIK</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.nik" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Name</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.fullname" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Gender</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.gendername" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Tanggal Lahir</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.birth_day" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Email</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.email" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">No. Telepon</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.phone" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Alamat</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.address" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Provinsi</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.province.name" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Kota</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.city.name" />
          </ui-col>
        </ui-row>
        <ui-row form-group>
          <ui-col size="md-1">Kode Pos</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.post_code" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Ahli Waris</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.benificiery" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Hubungan Ahli Waris</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.benificiery_relation" />
          </ui-col>
        </ui-row>

        <!-- 

        <ui-row form-group>
          <ui-col size="md-1">NPWP</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.npwp" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">NPWP Registration Date</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.npwpRegistrationDate" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">NPWP Address</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.npwpAddress" />
          </ui-col>
        </ui-row> -->

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />

      <ui-button icon="edit"
      href="#/update-profile/edit-data" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        form: {},
      };
    },

    computed: {
      ...mapGetters('AccountStore', {
        rowActive: 'myAccount',
      }),
    },

    mounted() {
      this.refreshData();  
    },

    methods: {

      ...mapActions('AccountStore', {
        reload: 'reload',
      }),

      async refreshData() {
        this.loading = true;

        await this.reload();
        this.form = this.rowActive.member;

        this.loading = false;
      },

    },

  }
</script>



