<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Update Bank Account" icon="edit" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-1">Bank Name</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.bank_name" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Bank Account Name</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.bank_acc_name" />
          </ui-col>
        </ui-row>
        
        <ui-row form-group>
          <ui-col size="md-1">Bank Account Number</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.bank_acc_number" />        
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="success"
      icon="arrow-left" 
      :disabled="loading" />

      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
    
        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {},
      };
    },

    computed: {
      ...mapGetters('UpdateBankAccountStore', {
        rowActive: 'rowActive',
      }),
    },

    async mounted() {
      this.loading = true;

      await this.fetchData();
      this.form = this.rowActive;
      
      this.loading = false;
    },

    methods: {

      ...mapActions('UpdateBankAccountStore', {
        fetchData: 'fetchData',
        storeData: 'storeData',
      }),

      saveData() {
        this.loading = true;

        this.storeData(this.form).then(result => {

          this.result = result;
          this.splash = true;

          setTimeout(() => {
            this.splash = false;
            this.loading = false;

            this.$router.go(-1);
          }, 2000);

        }).catch(result => {

          this.loading = false;
          this.result = result;

        });
      },

    },

  }
</script>



