import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

import LoginView from './views/login/LoginView';
import HomeView from './views/dashboard/DashboardView';
import KitchenView from './views/kitchen/KitchenView';

import LandingPageDetailDataView from './views/landing-page/LandingPageDetailDataView';
import LandingPageEditDataView from './views/landing-page/LandingPageEditDataView';

import UpdateProfileDetailDataView from './views/update-profile/UpdateProfileDetailDataView';
import UpdateProfileEditDataView from './views/update-profile/UpdateProfileEditDataView';

import UpgradeMembershipPackageView from './views/upgrade-membership/UpgradeMembershipPackageView';
import UpgradeMembershipOrderView from './views/upgrade-membership/UpgradeMembershipOrderView';

import UpdateBankAccountDetailDataView from './views/update-bank-account/UpdateBankAccountDetailDataView';
import UpdateBankAccountEditDataView from './views/update-bank-account/UpdateBankAccountEditDataView';

import UpdatePhoneNumberDetailDataView from './views/update-phone-number/UpdatePhoneNumberDetailDataView';
import UpdatePhoneNumberEditDataView from './views/update-phone-number/UpdatePhoneNumberEditDataView';

import ChangePasswordEditDataView from './views/change-password/ChangePasswordEditDataView';

import ActiveMembersAllDataView from './views/active-members/ActiveMembersAllDataView';
import ActiveMembersDetailDataView from './views/active-members/ActiveMembersDetailDataView';

import PendingMembersAllDataView from './views/pending-members/PendingMembersAllDataView';
import PendingMembersDetailDataView from './views/pending-members/PendingMembersDetailDataView';

import BonusSponsorAllDataView from './views/bonus-sponsor/BonusSponsorAllDataView';
import BonusSponsorDetailDataView from './views/bonus-sponsor/BonusSponsorDetailDataView';

import BonusRewardAllDataView from './views/bonus-reward/BonusRewardAllDataView';
import BonusRewardDetailDataView from './views/bonus-reward/BonusRewardDetailDataView';
import BonusRewardDetailKlaimDataView from './views/bonus-reward/BonusRewardDetailKlaimDataView';

import BonusPairingAllDataView from './views/bonus-pairing/BonusPairingAllDataView';
import BonusPairingDetailDataView from './views/bonus-pairing/BonusPairingDetailDataView';

import BonusQualifiedAllDataView from './views/bonus-qualified/BonusQualifiedAllDataView';
import BonusQualifiedDetailDataView from './views/bonus-qualified/BonusQualifiedDetailDataView';

import BonusSuperMatchAllDataView from './views/bonus-super-match/BonusSuperMatchAllDataView';
import BonusSuperMatchDetailDataView from './views/bonus-super-match/BonusSuperMatchDetailDataView';

import BonusDiscountAllDataView from './views/bonus-discount/BonusDiscountAllDataView';
import BonusDiscountDetailDataView from './views/bonus-discount/BonusDiscountDetailDataView';

import BonusCashbackAllDataView from './views/bonus-cashback/BonusCashbackAllDataView';
import BonusCashbackDetailDataView from './views/bonus-cashback/BonusCashbackDetailDataView';

import BonusLeadershipAllDataView from './views/bonus-leadership/BonusLeadershipAllDataView';
import BonusLeadershipDetailDataView from './views/bonus-leadership/BonusLeadershipDetailDataView';

import BonusSharingOmzetAllDataView from './views/bonus-sharing-omzet/BonusSharingOmzetAllDataView';
import BonusSharingOmzetDetailDataView from './views/bonus-sharing-omzet/BonusSharingOmzetDetailDataView';

import StatementBonusAllDataView from './views/statement-bonus/StatementBonusAllDataView';

import RiwayatBonusDanPajakAllDataView from './views/riwayat-bonus-dan-pajak/RiwayatBonusDanPajakAllDataView';
import RiwayatBonusDanPajakDetailDataView from './views/riwayat-bonus-dan-pajak/RiwayatBonusDanPajakDetailDataView';

import RiwayatUvAllDataView from './views/riwayat-uv/RiwayatUvAllDataView';
import RiwayatUvEditDataView from './views/riwayat-uv/RiwayatUvEditDataView';

import GenealogySponsorAllDataView from './views/genealogy-sponsor/GenealogySponsorAllDataView';
import GenealogySponsorEditDataView from './views/genealogy-sponsor/GenealogySponsorEditDataView';

import SaldoTransaksiEwalletAllDataView from './views/saldo-transaksi-ewallet/SaldoTransaksiEwalletAllDataView';
import SaldoTransaksiEwalletEditDataView from './views/saldo-transaksi-ewallet/SaldoTransaksiEwalletEditDataView';

import TransferToEcashEditDataView from './views/transfer-to-ecash/TransferToEcashEditDataView';

import EwalletWithdrawalAllDataView from './views/ewallet-withdrawal/EwalletWithdrawalAllDataView';
import EwalletWithdrawalEditDataView from './views/ewallet-withdrawal/EwalletWithdrawalEditDataView';

import SaldoTransaksiEcashAllDataView from './views/saldo-transaksi-ecash/SaldoTransaksiEcashAllDataView';
import SaldoTransaksiEcashEditDataView from './views/saldo-transaksi-ecash/SaldoTransaksiEcashEditDataView';

import TransferToMemberEditDataView from './views/transfer-to-member/TransferToMemberEditDataView';

import SalesOrderTransactionAllDataView from './views/sales-order-transaction/SalesOrderTransactionAllDataView';
import SalesOrderTransactionEditDataView from './views/sales-order-transaction/SalesOrderTransactionEditDataView';

import SalesOrderByRegisterAllDataView from './views/sales-order-by-register/SalesOrderByRegisterAllDataView';
import SalesOrderByRegisterEditDataView from './views/sales-order-by-register/SalesOrderByRegisterEditDataView';

import NewSalesOrderFormView from './views/new-sales-order/NewSalesOrderFormView';

import NetworkRegisterTreeView from './views/network-register/NetworkRegisterTreeView';
import NetworkRegisterPackageView from './views/network-register/NetworkRegisterPackageView';
import NetworkRegisterOrderView from './views/network-register/NetworkRegisterOrderView';
import NetworkRegisterFormView from './views/network-register/NetworkRegisterFormView';

import FreeRegisterPackageView from './views/free-register/FreeRegisterPackageView';
import FreeRegisterOrderView from './views/free-register/FreeRegisterOrderView';
import FreeRegisterFormView from './views/free-register/FreeRegisterFormView';

import RegistrationSimulationAllDataView from './views/registration-simulation/RegistrationSimulationAllDataView';
import RegistrationSimulationDetailDataView from './views/registration-simulation/RegistrationSimulationDetailDataView';
import RegistrationSimulationAddNewDataView from './views/registration-simulation/RegistrationSimulationAddNewDataView';

import GenealogyJaringanAllDataView from './views/genealogy-jaringan/GenealogyJaringanAllDataView';
import GenealogyJaringanDetailDataView from './views/genealogy-jaringan/GenealogyJaringanDetailDataView';
import GenealogyJaringanAddNewDataView from './views/genealogy-jaringan/GenealogyJaringanAddNewDataView';

export default new Router({
  routes: [
    { path: '/login', component: LoginView },
    { path: '/', component: HomeView },
    { path: '/kitchen', component: KitchenView },

    { path: '/landing-page', component: LandingPageDetailDataView },
    { path: '/landing-page/edit-data', component: LandingPageEditDataView },

    { path: '/update-profile', component: UpdateProfileDetailDataView },
    { path: '/update-profile/edit-data', component: UpdateProfileEditDataView },

    { path: '/upgrade-membership', component: UpgradeMembershipPackageView },
    { path: '/upgrade-membership/:paymentMethod/:package', component: UpgradeMembershipOrderView },

    { path: '/update-bank-account', component: UpdateBankAccountDetailDataView },
    { path: '/update-bank-account/edit-data', component: UpdateBankAccountEditDataView },

    { path: '/update-phone-number', component: UpdatePhoneNumberDetailDataView },
    { path: '/update-phone-number/edit-data', component: UpdatePhoneNumberEditDataView },

    { path: '/change-password', component: ChangePasswordEditDataView },

    { path: '/active-members/all-data', component: ActiveMembersAllDataView },
    { path: '/active-members/detail-data/:id', component: ActiveMembersDetailDataView },

    { path: '/pending-members/all-data', component: PendingMembersAllDataView },
    { path: '/pending-members/detail-data/:id', component: PendingMembersDetailDataView },

    { path: '/bonus-sponsor/all-data', component: BonusSponsorAllDataView },
    { path: '/bonus-sponsor/detail-data/:id', component: BonusSponsorDetailDataView },

    { path: '/bonus-reward/all-data', component: BonusRewardAllDataView },
    { path: '/bonus-reward/detail-data/:id', component: BonusRewardDetailDataView },
    { path: '/bonus-reward/detail-data-klaim/:id', component: BonusRewardDetailKlaimDataView },

    { path: '/bonus-pairing/all-data', component: BonusPairingAllDataView },
    { path: '/bonus-pairing/detail-data/:id', component: BonusPairingDetailDataView },

    { path: '/bonus-qualified/all-data', component: BonusQualifiedAllDataView },
    { path: '/bonus-qualified/detail-data/:id', component: BonusQualifiedDetailDataView },

    { path: '/bonus-super-match/all-data', component: BonusSuperMatchAllDataView },
    { path: '/bonus-super-match/detail-data/:id', component: BonusSuperMatchDetailDataView },

    { path: '/bonus-discount/all-data', component: BonusDiscountAllDataView },
    { path: '/bonus-discount/detail-data/:id', component: BonusDiscountDetailDataView },

    { path: '/bonus-cashback/all-data', component: BonusCashbackAllDataView },
    { path: '/bonus-cashback/detail-data/:id', component: BonusCashbackDetailDataView },

    { path: '/bonus-leadership/all-data', component: BonusLeadershipAllDataView },
    { path: '/bonus-leadership/detail-data/:id', component: BonusLeadershipDetailDataView },

    { path: '/bonus-sharing-omzet/all-data', component: BonusSharingOmzetAllDataView },
    { path: '/bonus-sharing-omzet/detail-data/:id', component: BonusSharingOmzetDetailDataView },

    { path: '/statement-bonus/all-data', component: StatementBonusAllDataView },

    { path: '/riwayat-bonus-dan-pajak/all-data', component: RiwayatBonusDanPajakAllDataView },
    { path: '/riwayat-bonus-dan-pajak/detail-data/:id', component: RiwayatBonusDanPajakDetailDataView },

    { path: '/riwayat-uv/all-data', component: RiwayatUvAllDataView },
    { path: '/riwayat-uv/edit-data/:id', component: RiwayatUvEditDataView },

    { path: '/genealogy-sponsor/all-data', component: GenealogySponsorAllDataView },
    { path: '/genealogy-sponsor/edit-data/:id', component: GenealogySponsorEditDataView },

    { path: '/saldo-transaksi-ewallet/all-data', component: SaldoTransaksiEwalletAllDataView },
    { path: '/saldo-transaksi-ewallet/edit-data/:id', component: SaldoTransaksiEwalletEditDataView },

    { path: '/transfer-to-ecash', component: TransferToEcashEditDataView },

    { path: '/ewallet-withdrawal/all-data', component: EwalletWithdrawalAllDataView },
    { path: '/ewallet-withdrawal/edit-data/:id', component: EwalletWithdrawalEditDataView },

    { path: '/saldo-transaksi-ecash/all-data', component: SaldoTransaksiEcashAllDataView },
    { path: '/saldo-transaksi-ecash/edit-data/:id', component: SaldoTransaksiEcashEditDataView },

    { path: '/transfer-to-member', component: TransferToMemberEditDataView },

    { path: '/sales-order-transaction/all-data', component: SalesOrderTransactionAllDataView },
    { path: '/sales-order-transaction/edit-data/:id', component: SalesOrderTransactionEditDataView },

    { path: '/sales-order-by-register/all-data', component: SalesOrderByRegisterAllDataView },
    { path: '/sales-order-by-register/edit-data/:id', component: SalesOrderByRegisterEditDataView },
    
    { path: '/new-sales-order', component: NewSalesOrderFormView },

    { path: '/network-register', component: NetworkRegisterTreeView },
    { path: '/network-register/:upline/:position', component: NetworkRegisterPackageView },
    { path: '/network-register/:upline/:position/:paymentMethod/:package', component: NetworkRegisterOrderView },
    { path: '/network-register/:upline/:position/:paymentMethod/:package/register', component: NetworkRegisterFormView },

    { path: '/free-register', component: FreeRegisterPackageView },
    { path: '/free-register/:paymentMethod/:package', component: FreeRegisterOrderView },
    { path: '/free-register/:paymentMethod/:package/register', component: FreeRegisterFormView },
    
    { path: '/registration-simulation', component: RegistrationSimulationAllDataView },
    { path: '/registration-simulation/:upline', component: RegistrationSimulationDetailDataView },
    { path: '/registration-simulation/:upline/:package', component: RegistrationSimulationAddNewDataView },

    { path: '/genealogy-jaringan', component: GenealogyJaringanAllDataView },
    { path: '/genealogy-jaringan/:upline', component: GenealogyJaringanDetailDataView },
    { path: '/genealogy-jaringan/:upline/:package', component: GenealogyJaringanAddNewDataView },

  ],
});



