export default {
  namespaced: true,

  state: {
    rs: [],
    rowActive: {},
  },

  mutations: {

    setDataSource(state, dataSource) {
      state.rs = dataSource;
    },

    setData(state, data) {
      state.rs.splice(state.rowActive.index, 1, {
        ...state.rs[state.rowActive.index],
        ...data,
      });
    },

    setRowActiveByIndex(state, index) {
      let rowActive = state.rs[index];
      rowActive.index = index;

      state.rowActive = rowActive;
    },

    setRowActiveById(state, id) {

      let rowActive = {};
      let i = -1;

      state.rs.forEach(row => {
        i++;

        if (row.id == id) {
          rowActive = row;
          rowActive.index = i;
        }

      });

      state.rowActive = rowActive;
    },

  },

  actions: {

    fetchData({ commit },data) {
      return new Promise((resolve, reject) => {
        api.post('genealogySponsor/readAllData', data).then(result => {

          if (result.state == 'success') {
            commit('setDataSource', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    moveData({ commit }, id) {
      commit('setRowActiveById', id);
    },

  },

  getters: {

    rs(state) {
      // return state.rs;
      return state.rs.map(row => {
        row.bv = `Fullname : ${row.member.fullname} / Sponsored : ${row.sponsor.fullname}`;
        return row;
      });
    },

    rowActive(state) {
      return state.rowActive;
    },

  },

}



