\<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-panel title="Free Register" icon="users">
        <h5>Please select the package member;</h5>
        <hr>
        <ui-row>

          <ui-col v-for="(memberPackage, i) in rsMemberPackage" 
          :key="i"
          size="xs-6">
            <!-- :value="parseInt(memberPackage.price).formatMoney()"  -->
            <ui-box icon="cube" 
            @click="moveData(memberPackage.id)"
            title="Pay Transfer" 
            :value="memberPackage.name" 
            :href="getRegisterUrl(memberPackage.id)" />
          </ui-col>

        </ui-row>        
        <hr>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
      };
    },

    mounted() {
      this.refreshData();
    },

    computed: {

      ...mapGetters('MemberPackageStore', {
        rsMemberPackage: 'rs',
      }),

    },

    methods: {

      ...mapActions('MemberPackageStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
      }),

      getRegisterUrl(id) {
        this.moveData(id);
        return '#/free-register/transfer/' + id;
      },
      
      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

    },

  }
</script>



