export default {
  namespaced: true,

  state: {
    rs: [],
    rowExtra: {},
    rowActive: {},
  },

  mutations: {

    setDataSource(state, dataSource) {
      state.rs = dataSource;
    },

    setDataExtra(state, dataSource) {
      state.rowExtra = dataSource;
    },

    setData(state, data) {
      state.rs.splice(state.rowActive.index, 1, {
        ...state.rs[state.rowActive.index],
        ...data,
      });
    },

    setRowActiveByIndex(state, index) {
      let rowActive = state.rs[index];
      rowActive.index = index;

      state.rowActive = rowActive;
    },

    setRowActiveById(state, id) {

      let rowActive = {};
      let i = -1;

      state.rs.forEach(row => {
        i++;

        if (row.id == id) {
          rowActive = row;
          rowActive.index = i;
        }

      });

      state.rowActive = rowActive;
    },

  },

  actions: {

    fetchData({ commit }) {
      return new Promise((resolve, reject) => {
        api.post('saldoTransaksiEwallet/readAllData', {
          computed: true
        }).then(result => {

          if (result.state == 'success') {
            commit('setDataSource', result.data);
            commit('setDataExtra', result.extra);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    moveData({ commit }, id) {
      commit('setRowActiveById', id);
    },
    
  },

  getters: {

    rs(state) {
      
      return state.rs.map(row => {
        row.bv = `Credit: ${row.credit} / Debit: ${row.debit}`;
        row.date = `Date: ${row.created_on} `;
        return row;
      });
    },

    rowActive(state) {
      return state.rowActive;
    },

    rowExtra(state) {
      return state.rowExtra;
    },

  },

}



