export default {
  namespaced: true,

  /************************
  function _state()
  ************************/
  state: {
    rs: [
      { id: 1, level: 'Level 1' },
      { id: 2, level: 'Level 2' },
      { id: 3, level: 'Level 3' },
      { id: 4, level: 'Level 4' },
      { id: 5, level: 'Level 5' },
      { id: 6, level: 'Level 6' },
      { id: 7, level: 'Level 7' },
      { id: 8, level: 'Level 8' },
      { id: 9, level: 'Level 9' },
      { id: 10, level: 'Level 10' },
    ],
  },

  /************************
  function _getters()
  ************************/
  getters: {

    rs(state) {
      return state.rs;
    },

  },

}



