<style scoped>
  .box {
    display: block;
    border: solid silver 1px;
    width: 128px;
    height: 160px;
    text-align: center;
    padding: 8px;
    position: relative;
    /* box-shadow:silver 2px 2px 2px; */
  }

  .box a {
    display: block;
  }

  .box figure img {
    width: 100px;
    height: 100px;
  }

  .box figure i {
    margin-top: 8px;
    font-size: 96px;
    color: #666;
  }

  .box .caption {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    border-top: solid silver 1px;
  }

  .box span {
    margin: 0;
    font-size: 13px;
    display: block;
  }

  .box sup {
    margin-top: -4px;
    color: #666;
  }

  .box-center {
    margin: 0 auto;
  }

  .box-left {
    float: left;
  }

  .box-right {
    float: right;
  }
</style>

<template>
  <div class="box" :class="'box-' + hrAlign">
    <a v-if="dataSource.id" @click="handleClick(true)" href="javascript:">
      <figure>
        <img v-if="dataSource.avatar"
        :src="getAvatar(dataSource)" 
        alt="">
        <i v-else class="fa fa-user"></i>
      </figure>
      <div class="caption">
        <span>{{ dataSource.fullname }}</span>
        <sup>- {{ dataSource.typename }} -</sup>  
      </div>    
    </a>
    <a v-else @click="handleClick(false)" href="javascript:">
      <figure>
        <i class="fa fa-user"></i>
      </figure>
      <div class="caption">
        <span>Register</span>
        <sup>- Empty -</sup>
      </div>
    </a>
  </div>
</template>

<script>
  export default {

    props: {

      dataSource: {
        type: Object,
        default: () => {},
      },
      
      hrAlign: {
        type: String,
        default: 'center',
      },

    },

    methods: {

      getAvatar(member) {
        return localStorage.archivesUrl + '/images/member/' + member.id + '/large_' + member.avatar;
      },

      handleClick(available) {
        this.$emit('click', {
          member: this.dataSource,
          available,
        });
      },

    },

  }
</script>



