<template>
  <main-layout id="RiwayatBonusDanPajakEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Riwayat Bonus dan Pajak" icon="eye">


        <ui-row form-group>
          <ui-col size="md-2">Bonus</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.bonus"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Index Value</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.index_value"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Tax Netto</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.tax_netto"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Tax Persen</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.tax_persen"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Tax Value</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.tax_value"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Date</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.date"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Status</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.status"></ui-label>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />

    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          bonus: '',
          index_value: '',
          tax_netto: '',
          tax_persen: '',
          tax_value: '',
          date: '',
          status: '',
        },
      };
    },

    computed: {
      ...mapGetters('RiwayatBonusDanPajakStore', {
        rowActive: 'rowActive',
      }),
    },

    mounted() {
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.bonus = this.rowActive.bonus;
      this.form.index_value = this.rowActive.index_value;
      this.form.tax_netto = this.rowActive.tax_netto;
      this.form.tax_persen = this.rowActive.tax_persen;
      this.form.tax_value = this.rowActive.tax_value;
      this.form.date = this.rowActive.date;
      this.form.status = this.rowActive.status;

    },

    methods: {

      ...mapActions('RiwayatBonusDanPajakStore', {
        moveData: 'moveData',
        updateData: 'updateData',
      }),


    },

  }
</script>



