import Vuex from 'vuex';
import Vue from 'vue';

Vue.use(Vuex);

import MonthStore from './MonthStore';
import ProvinceStore from './ProvinceStore';
import CityStore from './CityStore';
import LevelStore from './LevelStore';
import PublicationStatusStore from './PublicationStatusStore';
import GenderStore from './GenderStore';
import MenuStore from './MenuStore';
import AccountStore from './AccountStore';
import DashboardStore from './DashboardStore';
import LandingPageStore from './LandingPageStore';
import UpdateBankAccountStore from './UpdateBankAccountStore';
import UpdatePhoneNumberStore from './UpdatePhoneNumberStore';
import ChangePasswordStore from './ChangePasswordStore';
import ActiveMembersStore from './ActiveMembersStore';
import PendingMembersStore from './PendingMembersStore';
import BonusSponsorStore from './BonusSponsorStore';
import BonusRewardStore from './BonusRewardStore';
import BonusPairingStore from './BonusPairingStore';
import BonusQualifiedStore from './BonusQualifiedStore';
import BonusSuperMatchStore from './BonusSuperMatchStore';
import BonusDiscountStore from './BonusDiscountStore';
import BonusCashbackStore from './BonusCashbackStore';
import BonusLeadershipStore from './BonusLeadershipStore';
import BonusSharingOmzetStore from './BonusSharingOmzetStore';
import StatementBonusStore from './StatementBonusStore';
import RiwayatBonusDanPajakStore from './RiwayatBonusDanPajakStore';
import RiwayatUvStore from './RiwayatUvStore';
import SaldoTransaksiEwalletStore from './SaldoTransaksiEwalletStore';
