import TransferToMemberStore from './TransferToMemberStore';
import GenealogySponsorStore from './GenealogySponsorStore';
import SalesOrderTransactionStore from './SalesOrderTransactionStore';
import SalesOrderByRegisterStore from './SalesOrderByRegisterStore';
import MemberNetworkStore from './MemberNetworkStore';
import MemberPackageStore from './MemberPackageStore';
import RegistrationSimulationStore from './RegistrationSimulationStore';
import RegisterMemberStore from './RegisterMemberStore';
import NetworkRegisterStore from './NetworkRegisterStore';
import FreeRegisterStore from './FreeRegisterStore';
import NewSalesOrderStore from './NewSalesOrderStore';
import UpgradeMembershipStore from './UpgradeMembershipStore';

const store = new Vuex.Store({
  
  modules: {
    MonthStore,
    ProvinceStore,
    CityStore,
    PublicationStatusStore,
    LevelStore,
    GenderStore,
    MenuStore,
    AccountStore,
    DashboardStore,
    LandingPageStore,
    UpdateBankAccountStore,
    UpdatePhoneNumberStore,
    ChangePasswordStore,
    ActiveMembersStore,
    PendingMembersStore,
    BonusSponsorStore,
    BonusPairingStore,
    BonusQualifiedStore,
    BonusSuperMatchStore,
    BonusDiscountStore,
    BonusCashbackStore,
    BonusRewardStore,
    BonusLeadershipStore,
    BonusSharingOmzetStore,
    StatementBonusStore,
    RiwayatBonusDanPajakStore,
    RiwayatUvStore,
    SaldoTransaksiEwalletStore,
    TransferToEcashStore,
    SaldoTransaksiEcashStore,
    TransferToMemberStore,
    GenealogySponsorStore,
    SalesOrderTransactionStore,
    SalesOrderByRegisterStore,
    MemberNetworkStore,
    MemberPackageStore,
    RegistrationSimulationStore,
    RegisterMemberStore,
    FreeRegisterStore,
    NetworkRegisterStore,
    NewSalesOrderStore,
    UpgradeMembershipStore,
  },

});

export default store;



