<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>
      
      <ui-panel title="Upgrade Membership" icon="cube">

        <!-- :value="parseInt(memberPackage.price).formatMoney()"  -->
        <ui-box icon="cube" 
        :title="(this.form.payment_method == 'ecash')?'Pay With Ecash':'Pay Transfer'" 
        :value="memberPackage.name" />
        <hr>
        
        <ui-row v-for="(row, i) in products" :key="i" form-group>
          <ui-col size="md-12">
            <b>{{ row.name }}</b><br>
            <i>Price: {{ parseInt(((form.package == 1)?row.specialcprice:row.specialpprice)).formatMoney() }}</i>
          </ui-col>
          <ui-col size="md-12">
            <!-- <input type="hidden" v-model="form.product_id[i]" :value="row.id"> -->
            <ui-textbox v-model="row.qty" placeholder="Qty" />
          </ui-col>
          <ui-col size="md-12">
            <i>Total: {{ parseInt(((form.package == 1)?row.specialcprice:row.specialpprice) * row.qty).formatMoney() }}</i>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="danger"
      icon="arrow-left" />

      <ui-button @click="showModal" 
      icon="floppy-o" />
    </ui-buttonbar>

    <ui-modal @close="modal = false" :display="modal" title="Review Your Order" icon="question">
      <div slot="message">

        <div style="height: 240px; over-flow-x: hidden; overflow-y: scroll">

          <div style="">
            <ul>
              <li>Pemesan: {{ myAccount.member.username }}</li>
              <li>Nama: {{ myAccount.member.fullname }}</li>
              <li>Dari: {{ myAccount.member.typename }}</li>
              <li>Upgrade Ke: <b>{{ memberPackage.name }}</b></li>
            </ul>
          </div>
          <hr>

          <div v-for="(row, i) in products" :key="i" form-group>
            <div v-if="parseInt(row.qty) > 0">
              <ui-col size="md-12">
                <b>{{ row.name }}</b><br>
                <i>Price: {{ parseInt(((form.package == 1)?row.specialcprice:row.specialpprice)).formatMoney() }}</i>
              </ui-col>
              
              <ui-col size="md-12">
                Qty: {{ (row.qty)?row.qty:0 }}
              </ui-col>
              <ui-col size="md-12">
                <i>Total: {{ parseInt(((form.package == 1)?row.specialcprice:row.specialpprice) * row.qty).formatMoney() }}</i>
              </ui-col>
              <hr>
            </div>
          </div>
          
          <div style="text-align: center; color: red; font-size: 16px">
            <b>TOTAL: {{ total.formatMoney() }}</b>
          </div>
        </div>

      </div>
      <div slot="buttonbar">
        <ui-button @click="saveData"
        :loading="loading" >Konfirmasi</ui-button>

        <ui-button @click="modal = false" 
        bs-type="danger">Cancel</ui-button>
      </div>
    </ui-modal>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        modal: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          package: '',
          payment_method: '',
          order: {
            quantity: [],
            product_id: [],
            stock_product_id: [],
          },
        },
      };
    },

    computed: {
      ...mapGetters('UpgradeMembershipStore', {
        products: 'products',
        data: 'data',
      }),

      ...mapGetters('AccountStore', {
        myAccount: 'myAccount',
      }),

      ...mapGetters('MemberPackageStore', {
        memberPackage: 'rowActive',
      }),

      total() {
        let total = 0;

        this.products.forEach((row) => {
          if (row.qty)
            total += parseInt(((this.form.package == 1)?row.specialcprice:row.specialpprice) * row.qty);
        });

        return total;
      },
    },

    mounted() {
      this.reloadParams();
      this.refreshData();
    },

    methods: {
      
      ...mapActions('UpgradeMembershipStore', {
        fetchData: 'fetchData',
        createData: 'createData',
      }),

      reloadParams() {
        this.form.payment_method = this.$route.params.paymentMethod;
        this.form.package = this.$route.params.package;
      },

      refreshData() {
        let data = {
          package: this.$route.params.package,
        };

        this.loading = true;
        this.fetchData(data).then(result => {
          this.loading = false;
        });
      },

      showModal() {
        if (this.total > 0) this.modal = true;
      },

      saveData() {
        
        this.products.forEach((row, i) => {
          this.form.order.product_id[i] = row.id;
          this.form.order.stock_product_id[i] = row.id;
          this.form.order.quantity[i] = (row.qty?parseInt(row.qty):0); 
        });

        this.loading = true;

        this.createData(this.form).then(result => {
          this.loading = false;
          this.modal = false;
          
          this.result = result;

          this.splash = true;

          setTimeout(() => {
            this.splash = false;

            setTimeout(() => {
              this.$router.push('/upgrade-membership');
            }, 500);
          }, 1500);

        }).catch(result => {

          this.loading = false;
          this.modal = false;

          this.result = result;

        });
      },

    },

  }
</script>



