<template>
  <main-layout id="HomeView">

    <ui-container fluid>

      <ui-panel title="Kitchen">
        <ui-row form-group>
          <ui-col size="md-12">
            <ui-button @click="handleTest" bs-type="danger">Test</ui-button>
            <ui-button>Test</ui-button>
            <ui-button>Test</ui-button>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-12">
            <ui-alert :data-source="rs" data-field="pekerjaan" :display="showError">Testa</ui-alert>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-12">
            <ui-table :data-source="rsPost1" hover>
              <tr>
                <th>satu</th>
                <th>satu</th>
              </tr>
            </ui-table>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Textbox</ui-col>
          <ui-col size="md-11">
            <ui-textbox></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Datepicker</ui-col>
          <ui-col size="md-11">
            <ui-datepicker></ui-datepicker>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Image</ui-col>
          <ui-col size="md-3">
            <ui-image v-model="image"></ui-image>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Checkbox</ui-col>
          <ui-col size="md-11">
            <ui-checkbox :data-source="rs"></ui-checkbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Radio</ui-col>
          <ui-col size="md-11">
            <ui-radio :data-source="rs"></ui-radio>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Select</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rs" data-field="pekerjaan">Testa</ui-select>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Textarea</ui-col>
          <ui-col size="md-11">
            <ui-textarea></ui-textarea>
          </ui-col>
        </ui-row>

      </ui-panel>

    </ui-container>

    <ui-buttonbar>
      <ui-button icon="floppy-o"></ui-button>
      <ui-button icon="home"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        tanggal: '2012-4-12',
        testa: [1, 3],
        image: '',
        showError: false,
        rs: [
          { id: 1, pekerjaan: 'Programmer', kantor: 'satu' },
          { id: 2, pekerjaan: 'Backend', kantor: '<a href="#/test">dua</a>' },
          { id: 3, pekerjaan: 'Frontend', kantor: 'tiga' },
        ],
        rs1: [
          { id: 1, pekerjaan: 'Programmer1' },
          { id: 2, pekerjaan: 'Backend1' },
          { id: 3, pekerjaan: 'Frontend1' },
        ],
        textboxLoading: false,
      };
    },

    /************************
    <id="_computed">
    ************************/
    computed: {

      ...mapGetters('Post', {
        rsPost: 'rs',
        rowPostActive: 'rowActive',
      }),

      rsPost1() {
        let rs = [];
        this.rsPost.forEach((row) => {
          rs.push({
            id: row.id,
            title: row.title,
            link: '<a href="#/page/' + row.id + '">test</a>',
          });
        });

        return rs;
      }
    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
      this.$store.dispatch('Post/fetchData').then(result => {
        // alert(result.message);
      }).catch(result => {
        alert(result.message);
      });
    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_mapActions">
      ************************/
      ...mapActions('Post', {
        moveRowPostById: 'moveRowById',
      }),

      /************************
      <id="_handleEnter">
      ************************/
      handleEnter() {
        this.textboxLoading = true;
      },

      /************************
      <id="_handleTest">
      ************************/
      handleTest() {
        // this.image = 'box-package.png';
        this.showError = !this.showError;
        // this.$store.dispatch('Post/moveRowById', 3);
        this.moveRowPostById(2);
      },

      /************************
      <id="_handleTanggal">
      ************************/
      handleTanggal() {
        console.log(this.tanggal);
      },
    },

  }

</script>



